.checkOutBox {
  position: fixed;
  top: 100px;
  right: 0;
  height: 100%;
  width: 25%;
  background-color: white;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  z-index: 2;
}
.scrollContainer {
  overflow-y: auto;
  height: 30%;
  overflow-x: hidden;
}
/* width */
.scrollContainer::-webkit-scrollbar {
  width: 5px;
}

/* Track */
.scrollContainer::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
.scrollContainer::-webkit-scrollbar-thumb {
  background: gray;
  border-radius: 1px;
}

/* Handle on hover */
.scrollContainer::-webkit-scrollbar-thumb:hover {
  background: gray;
}

.fntszLg {
  font-size: 15px;
}

.couponForm {
  height: 80px;
}
.activeCoupon {
  border: 1px solid red !important;
}
.activeCouponMessage {
  color: lime !important;
}
@media only screen and (max-width: 600px) {
  .checkOutBox {
    position: fixed;
    right: 0;
    height: 100%;
    width: 100%;
    background-color: white;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    z-index: 2;

    transition: opacity 1s ease-out;
    opacity: 0;
    height: 0;
    overflow: hidden;
  }

  .active {
    opacity: 1;
    height: 100%;
  }
  .scrollContainer {
    overflow-y: auto;
    height: 20%;
    overflow-x: hidden;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .price {
    font-size: 15px !important;
  }

  .card p {
    font-size: 10px;
  }
  .card h5 {
    font-size: 15px;
  }

  .checkOutBox {
    font-size: 12px;
    z-index: 2;
  }

  .checkOutBox {
    width: 35%;
  }
}
