.formContainer {
  width: 75%;
}

.formContainer form {
  width: 60%;
  margin: auto;
  font-size: 20px;
}

#checkoutForm .form-group {
  margin-bottom: 0;
  margin-top: 10px;
  height: 90px;
  z-index: 999 !important;
}
.textarea {
  height: 200px !important;
}
.active {
  border: 1px solid red;
}
.errorDisp {
  color: red;
  font-size: small;
  padding: 5px;
  padding-top: 10px;
  margin-top: -10px;
  border-radius: 5px;
  z-index: 0 !important;
}
#formBody {
  background-color: white;
}

@media only screen and (max-width: 600px) {
  .formContainer {
    width: 95%;
    margin: auto;
  }
  .formContainer form {
    width: 100%;
    margin: auto;
    font-size: 20px;
  }
}
